import './vendor/body-scroll-lock';
// import Swiper from 'swiper/bundle';

import PerfectScrollbar from 'perfect-scrollbar';
const container = document.querySelector('.comparison__table-wrapper');
if (container) { //             = )
  const ps = new PerfectScrollbar(container, {
    wheelSpeed: 2,
    wheelPropagation: true,
    minScrollbarLength: 2,
    maxScrollbarLength: 48,
    suppressScrollY: true,
  });

  let comparisonScroll = document.querySelector('.ps__rail-x');
  comparisonScroll.classList.add('comparison__scrollbar');
  container.scrollTop = 10;
  ps.update();
}

document.addEventListener('click', (evt) => {
  if (evt.target.closest('[data-scroll="right"]')) {
    if (container) { //             = )
      container.scrollBy({
        left: 100,
        behavior: 'smooth',
      });
    }
  } else if (evt.target.closest('[data-scroll="left"]')) {
    if (container) { //             = )
      container.scrollBy({
        left: -100,
        behavior: 'smooth',
      });
    }
  }
});
